import React from "react";
import Typewriter from "typewriter-effect";

import { Player } from "@lottiefiles/react-lottie-player";
import Technology from "../home/Technology";
import SectionTitle from "../../components/SectionTitle";
import Intro from "./Intro";
import Experience from "./Experience";
import Projects from "./Projects";

const Home = () => {
  const name = "Muhammad Ahmed Fayyaz.";
  return (
    <>
      <div className="bg-primary  sm:h-screen  sm:items-center overflow-hidden sm:w-full flex sm:flex-col px-32 md:px-16 sm:px-10">
        <div className="flex bg-primary flex-col gap-9 sm:gap-6 md:gap-6 py-16  w-1/8  sm:w-100">
          <h1 className="text-white bg-primary">Hi, I am</h1>
          <h1 className="typing  text-secondary bg-primary text-5xl md:text-4xl sm:text-2xl font-bold">
            <Typewriter
              onInit={(typewriter) => {
                typewriter
                  .typeString(`${name}`)
                  // .callFunction(() => {
                  //   console.log("String typed out!");
                  // })
                  // .pauseFor(2500)
                  // .deleteAll()
                  // .callFunction(() => {
                  //   console.log("All strings were deleted");
                  // })
                  .start();
              }}
            />
          </h1>
          <h1 className="text-white text-5xl bg-primary md:text-4xl sm:text-2xl  font-bold ">
            I build things for the web.
          </h1>
          <p className="text-white w-[50vw] sm:w-[75vw]  bg-primary p-2">
            Greetings! I am a highly motivated and proactive web developer with
            a strong passion for creating remarkable solutions. My name is
            Muhammad Ahmed Fayyaz , and I am dedicated to learning and building
            fantastic things in the field of web development.
          </p>
          <button className="text-tertiary  border  border-tertiary w-40 py-3 rounded hover:bg-tertiary  hover:text-primary hover:font-semibold transition duration-600 ease-in-out">
            <a href="/"> Get Started</a>
          </button>
        </div>
        <div className=" flex   bg-primary w-1/6  sm:w-full px-14 py-20  sm:items-center sm:justify-center sm:hidden md:hidden">
          <Player
            autoplay
            loop
            src="https://lottie.host/a7a8be23-c9ca-426e-a8b1-16bbd8d84afd/gwYCJSBbxi.json"
            style={{ height: "350px", width: "350px" }}>
            {/* <Controls
              visible={true}
               buttons={["play", "repeat", "frame", "debug"]}
            /> */}
          </Player>
        </div>
      </div>

      <SectionTitle title="About Me " />
      <Intro />
      <Technology />
      <Experience />
      <Projects />
    </>
  );
};

export default Home;
